<template>
  <div>
    <div>
    <v-row justify="center">
      <v-col>
        <h1 style="margin-left:450px; margin-top:20px">CITAS VETERINARIA </h1>
      </v-col>
    </v-row>
    <v-card class="form" style="background-color:rgba(144, 202, 249, 0.46);">    
      <h2>Agende su Cita</h2>
    <v-form    
  >
  <v-row>
    <v-col>
    <v-text-field
      v-model="editedItem.namePet"    
      label="Nombre de la Mascota"
      required
    ></v-text-field>
    </v-col>

    <v-col>
    <v-text-field 
      v-model="editedItem.propietario"      
      label="Nombre del Propietario"
      required
    ></v-text-field>

    </v-col>
  </v-row>

  <v-row>
    <v-col cols="4">
    <v-text-field
      v-model="editedItem.telefono"    
      label="Teléfono de Contacto"
      required
    ></v-text-field>
    </v-col>

    <v-col cols="8">
    <v-text-field
      v-model="editedItem.sintomas"      
      label="Síntomas"
      required
    ></v-text-field>

    </v-col>
  </v-row>

  <v-row>
    <v-col cols="4">
    <v-text-field
      type="time"
      v-model="editedItem.hora"    
      label="Hora de Cita "
      required
    ></v-text-field>
    </v-col>

    <v-col cols="4">
    <v-text-field
      type="date"
      v-model="editedItem.fecha"      
      label="Fecha de Cita "
      required
    ></v-text-field>
    </v-col>

    <v-col cols="4">
    <v-btn
      color="success"
      @click="agendar()"
    >
      AGENDAR
    </v-btn>

    </v-col>
  </v-row>

  </v-form>  
  </v-card>
  </div>

  <div>
    <v-card class="form" style="background-color:rgba(144, 202, 249, 0.46)">
      <h2>Citas Agendadas</h2>
    <v-text-field v-model="search" append-icon="mdi-magnify" label="Buscar" single-line hide-details ></v-text-field>
  <v-data-table
  :headers="headers"
  :items="citas"
  :search="search"
   style="margin-top:15px"></v-data-table>
    </v-card>
    
    <v-snackbar v-model="snackbar">
      {{ msg }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
  </div>
</template>

<script>
 import axios from 'axios'
  export default {  

    data(){
      return{
      search:"",
      snackbar:false,
      citas:[],
      msg:'Agendada con Exito',
      headers:[
        { text: 'Propietario', value: 'propietario',class:'teal accent-4 white--text' },
        { text: 'Nombre Mascota', value: 'namePet', class:'teal accent-4 white--text' },
        { text: 'Telefono', value: 'telefono', class:'teal accent-4 white--text' },
        { text: 'Sintomas', value: 'sintomas', class:'teal accent-4 white--text' },
        { text: 'Hora', value: 'hora', class:'teal accent-4 white--text' },
        { text: 'Fecha', value: 'fecha', class:'teal accent-4 white--text' },
        { text: 'Cita creada', value: 'createAt', class:'teal accent-4 white--text' },
      ],

      editedItem:{
        propietario:'',
        namePet:'',
        telefono:'',
        sintomas:'',
        hora:'',
        fecha:'',
      }
      };       
    },

    created(){
      this.listarCitas();
    },

    methods:{
      listarCitas(){
         axios
          .get("cita")
          .then((response) =>{
              console.log(response.data.formulario);
              this.citas = response.data.formulario;
          })
          .catch((error)=>{
              console.log(error.response);
          })
      },

      agendar(){
        const me = this;
        axios
        .post('cita',{
          namePet: this.editedItem.namePet,
          propietario: this.editedItem.propietario,
          telefono: this.editedItem.telefono,
          sintomas: this.editedItem.sintomas,
          hora: this.editedItem.hora,
          fecha: this.editedItem.fecha,          
        })
        .then((response)=>{
          console.log(response); 
          me.snackbar=true;         
          me.listarCitas();
          me.limpiarCajas();
        })
        .catch((error)=>{
          console.log(error.response);
        })
      }
    },
    msg(){
      this.snackbar=true;
      
    },

    limpiarCajas(){
          this.editedItem.namePet= "",
          this.editedItem.propietario="", 
          this.editedItem.telefono="", 
          this.editedItem.sintomas="", 
          this.editedItem.hora="", 
         this.editedItem.fecha="" 
    }
  }
</script>

<style>
.form{
  margin: 30px;
  padding: 20px;
  background-color: #90CAF9;
}
</style>